.project {
    position: relative;
    width: 100%;
    min-height: 500px;
    background-color: #FFFFFF;
    border: 1px solid #edeeee;
    border-radius: 10px;
    overflow: hidden;
}

.image-container {
    height: 250px;
    overflow: hidden;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: #edeeee;
}

.image {
    width: 100%;
}

.project-detail-container {
    position: relative;
    margin: 20px 20px;
}

.project-detail-text {
    line-height: 1.5;
    font-size: 15px;
    font-weight: 200;
    color: #9E9E9E;
}

.project-title {
    margin: 0;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 500;
}

.learn-more-btn {
    position: absolute;
    display: block;
    padding: 5px;
    bottom: 20px;
    left: 20px;
    border: 1px solid #BDBDBD;
    color: #BDBDBD;
    overflow: hidden;
    border-radius: 3px;
    cursor: pointer;
    outline: none;
    text-decoration: none;
    font-size: 12px;
    font-weight: 400;
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.learn-more-btn:hover {
    background-color: #F5F5F5;
}


