@font-face {
  font-family: alrightSans;
  font-weight: 100;
  src: url(./alright-light.otf);
}

@font-face {
  font-family: alrightSans;
  font-weight: 200;
  src: url(./alright-normal.otf);
}

.name {
  font-size: 90px;
  font-weight: 100;
  margin: 0;
}

.container {
  position: relative;
  width: 1180px;
  margin: 10% auto;
  -webkit-overflow-scrolling: touch;
}

.bio {
  display: inline-block;
}

.intro {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  position: relative;
  margin: 0;
  vertical-align: middle;
}

.circle {
  width: 5px;
  height: 5px;
  border-radius: 5px;
  margin: 10px;
}

.intro-text {
  float: left;
  text-align: left;
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.creative {
  background-color: #2196F3;
}

.logic {
  background-color: #E91E63;
}


.app {
  font-family: alrightSans;
  margin: 0;
}

.project-container {
  margin-top: 10%;
}

.project-title-container {
  /* border: 2px solid #000000; */
  background-color: #EEEEEE;
  display: inline-block;
  padding: 5px 20px;
}

.project-container-title {
  display: inline;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 200;
  font-size: 20px;
  margin: 0;
  color: #9E9E9E;
}

.project-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.project-list::after {
  content: " ";
  display: block; 
  height: 0; 
  clear: both;
}

.project-list-item {
  width: 45%;
  float: left;
  margin: 5% 5% 0 0;
}

@media only screen and (max-width: 1024px) {
  .container {
    text-align: center;
    width: 100%;
    -webkit-overflow-scrolling: touch;
  }

  .name {
    font-size: 40px;
  }

  .contact-bar {
    position: relative;
    top: 15px;
    left: 0;
  }

  .project-list-item {
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    float: none;
  }

  .learn-more-btn {
    position: relative;
    display: inline-block;
    left: 0;
    top: 0;
    margin-bottom: 20px;
  }
}


