.contact-bar {
    position: absolute;
    display: inline-block;
    top: 10px;
    right: 10px;
    font-size: 24px;
    margin: 20px 0;
  }

  .contact-link{
    color: #BDBDBD;
    margin: 0 10px;
  }

  .contact-link:hover{
    color: #757575;
  }

  .resume {
    display: block;
    text-align: center;
    margin-top: 10px;
    text-decoration: none;
    color: #4DB6AC;
    font-size: 12px;
    cursor: pointer;
  }


